
import { computed, defineComponent, PropType } from "vue";

export default defineComponent({
  name: "IndexedColor",
  props: {
    indexes: { type: Array as PropType<number[]>, default: () => [] },
    passive: Boolean,
  },
  setup(props) {
    const classes = computed(() =>
      props.indexes.map((i) => "colored-back-" + i)
    );
    return { classes };
  },
});
